<template>
  <v-content class="ma-0 pa-0">
    <v-row>
      <v-col sm="6" cols="12"> <app-panel :loading="loading" title="Subtotal"  :count="subtotal.count" :amount="subtotal.amount"></app-panel> </v-col>
      <v-col sm="6" cols="12"> <app-panel :loading="loading" title="Withdraw"  :count="withdraw.count" :amount="withdraw.amount" color="red"></app-panel> </v-col>
      <v-col sm="12" cols="12"> <app-panel :loading="loading" title="Total"  :count="total.count" :amount="total.amount"></app-panel> </v-col>
    </v-row>



<v-sheet  class="my-2"   width="100%" elevation="1" v-for="bank in banks" :key="bank._id" >
    <v-toolbar class="mb-3" dense flat  >
      <v-toolbar-title class="title ">{{bank.bank.number}} ({{getCountry(bank.bank.country)}})</v-toolbar-title>
    </v-toolbar>
 
  <v-row class="mx-1" >
      <v-col class="mx-2 pa-0 px-2" :cols="$vuetify.breakpoint.name == 'xs' ? 12 : null" sm="">
        <fieldset class="px-2 mb-2 fielset">
          <legend>Subtotal</legend>
          <v-row>
            <v-col ><small>Deposits</small><h4>{{ bank.subtotal ? bank.subtotal.count : 0 }}</h4></v-col>
            <v-col> <small>Total</small><h4>{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(bank.subtotal ? bank.subtotal.amount : 0)}}</h4></v-col>
          </v-row>
        </fieldset>
      </v-col> 
     <v-col class="mx-2 pa-0 px-2" :cols="$vuetify.breakpoint.name == 'xs' ? 12 : null" sm="">
        <fieldset class="px-2 mb-2 fielset">
          <legend class="red--text">Withdraw</legend>
          <v-row>
            <v-col ><small class="red--text">Deposits</small><h4 class="red--text">{{ bank.withdraw ? bank.withdraw.count : 0 }}</h4></v-col>
            <v-col> <small class="red--text">Total</small><h4 class="red--text">{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(bank.withdraw ? bank.withdraw.amount : 0)}}</h4></v-col>
          </v-row>
        </fieldset>
      </v-col> 
       <v-col class="mx-2 pa-0 px-2" :cols="$vuetify.breakpoint.name == 'xs' ? 12 : null" sm="">
        <fieldset class="px-2 mb-2 fielset">
          <legend>Total</legend>
          <v-row>
            <v-col ><small>Deposits</small><h4>{{ bank.count || 0 }}</h4></v-col>
            <v-col> <small>Total</small><h4>{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(bank.amount || 0)}}</h4></v-col>
          </v-row>
        </fieldset>
      </v-col>  
      <!--<v-col class="mx-2 pa-0 px-2" :cols="$vuetify.breakpoint.name == 'xs' ? 12 : null" sm="">
        <fieldset class="px-2 mb-2 fielset">
          <legend>Total USD</legend>
          <v-row>
            <v-col ><small>Deposits</small><h4>{{wakel.total ? wakel.total.count : 0 }}</h4></v-col>
            <v-col> <small>Total</small><h4>{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(wakel.total ? wakel.total.amount /3.9 : 0)}}</h4></v-col>
          </v-row>
        </fieldset>
      </v-col>   -->
    </v-row>  
     
  </v-sheet>

 
  </v-content>
</template>
<script>
import { mapActions } from 'vuex'
import AppPanel from './Panel'
import countries from './../../country'
export default {
  name: 'WakelsTotalDashboard',
  components: {
    AppPanel
  },
  props: {
    dates: Array
  },
  data () {
    return {
      loading: false,
      subtotal: { count: 0, amount: 0},
      withdraw: { count: 0, amount: 0},
      total: { count: 0, amount: 0},
      banks: []
    }
  },
  watch: {
    dates: {
      handler: 'fetchTotal',
      immediate: true
    }
  },
  methods: {
    ...mapActions({
      get: 'api/get'
    }),
    getCountry (code) {
      const filtered = countries.filter(e => e.code == code) 
      return filtered.length ? filtered[0].name : 'N/A'
    },
    async fetchTotal () { 
      // get totals
      this.loading = true
      const total = await this.get({ collection: 'banktransaction/total', query: {
        created_on: this.dates.join('|')
      } })
      if (total.status == 200 ) {
        const [ allTotal ] = total.data
        
        this.subtotal.amount = allTotal && allTotal.subtotal ? allTotal.subtotal.amount : 0
        this.subtotal.count = allTotal && allTotal.subtotal ? allTotal.subtotal.count : 0

        this.withdraw.amount = allTotal && allTotal.withdraw ? allTotal.withdraw.amount : 0
        this.withdraw.count = allTotal && allTotal.withdraw ? allTotal.withdraw.count : 0
      
        this.total.amount = allTotal && allTotal.amount ? allTotal.amount  : 0
        this.total.count = allTotal && allTotal.count ? allTotal.count   : 0
      
      }



      // total per bank
       const totalbank = await this.get({ collection: 'banktransaction/totalbank', query: {
        created_on: this.dates.join('|')
      } })

      this.banks = totalbank.data

      //
 

      
   
     
       


      this.loading = false
    }
  }
}
</script>
<style   scoped>
.fielset {
  border: 1px solid gray;
  border-radius: 5px;
}
</style>